'use client';

import React, { useState, useRef } from 'react';

interface ProductImage {
  id: string;
  url: string;
  cloudflare_image_id: string;
  is_primary: boolean;
  display_order: number;
}

interface ProductImageManagerProps {
  productId: string;
  images: ProductImage[];
  onImagesChange?: () => void;
}

export default function ProductImageManager({
  productId,
  images: initialImages,
  onImagesChange,
}: ProductImageManagerProps) {
  const [images, setImages] = useState<ProductImage[]>(initialImages || []);
  const [uploading, setUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState<string>('');
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleFileSelect = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = event.target.files;
    if (!files || files.length === 0) return;

    setUploading(true);
    setUploadProgress('Uploading images...');

    try {
      const formData = new FormData();
      formData.append('productId', productId);
      formData.append('isPrimary', images.length === 0 ? 'true' : 'false');

      Array.from(files).forEach((file) => {
        formData.append('images', file);
      });

      const response = await fetch('/api/products/images/upload', {
        method: 'POST',
        body: formData,
      });

      const data = await response.json();

      if (data.success) {
        setUploadProgress(`Successfully uploaded ${data.results.filter((r: any) => r.success).length} image(s)`);

        // Add new images to the list
        const newImages = data.results
          .filter((r: any) => r.success)
          .map((r: any) => ({
            id: r.imageId,
            url: r.url,
            cloudflare_image_id: r.cloudflareImageId,
            is_primary: r.isPrimary,
            display_order: images.length + data.results.indexOf(r),
          }));

        setImages([...images, ...newImages]);
        onImagesChange?.();

        setTimeout(() => setUploadProgress(''), 3000);
      } else {
        setUploadProgress(`Error: ${data.error}`);
      }
    } catch (error: any) {
      console.error('Upload error:', error);
      setUploadProgress(`Error: ${error.message}`);
    } finally {
      setUploading(false);
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
    }
  };

  const handleDeleteImage = async (imageId: string) => {
    if (!confirm('Are you sure you want to delete this image?')) return;

    try {
      const response = await fetch(`/api/products/images/${imageId}`, {
        method: 'DELETE',
      });

      const data = await response.json();

      if (data.success) {
        setImages(images.filter((img) => img.id !== imageId));
        onImagesChange?.();
      } else {
        alert(`Error: ${data.error}`);
      }
    } catch (error: any) {
      console.error('Delete error:', error);
      alert(`Error: ${error.message}`);
    }
  };

  const handleSetPrimary = async (imageId: string) => {
    try {
      const response = await fetch(`/api/products/images/${imageId}`, {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ isPrimary: true }),
      });

      const data = await response.json();

      if (data.success) {
        setImages(
          images.map((img) => ({
            ...img,
            is_primary: img.id === imageId,
          }))
        );
        onImagesChange?.();
      } else {
        alert(`Error: ${data.error}`);
      }
    } catch (error: any) {
      console.error('Set primary error:', error);
      alert(`Error: ${error.message}`);
    }
  };

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between">
        <h3 className="text-lg font-medium text-gray-900">Product Images</h3>
        <button
          onClick={() => fileInputRef.current?.click()}
          disabled={uploading}
          className="px-4 py-2 bg-amber-600 text-white rounded-md hover:bg-amber-700 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          {uploading ? 'Uploading...' : 'Add Images'}
        </button>
        <input
          ref={fileInputRef}
          type="file"
          multiple
          accept="image/*"
          onChange={handleFileSelect}
          className="hidden"
        />
      </div>

      {uploadProgress && (
        <div className={`p-3 rounded-md ${uploadProgress.startsWith('Error') ? 'bg-red-50 text-red-700' : 'bg-green-50 text-green-700'}`}>
          {uploadProgress}
        </div>
      )}

      {images.length === 0 ? (
        <div className="text-center py-12 border-2 border-dashed border-gray-300 rounded-lg">
          <svg
            className="mx-auto h-12 w-12 text-gray-400"
            stroke="currentColor"
            fill="none"
            viewBox="0 0 48 48"
          >
            <path
              d="M28 8H12a4 4 0 00-4 4v20m32-12v8m0 0v8a4 4 0 01-4 4H12a4 4 0 01-4-4v-4m32-4l-3.172-3.172a4 4 0 00-5.656 0L28 28M8 32l9.172-9.172a4 4 0 015.656 0L28 28m0 0l4 4m4-24h8m-4-4v8m-12 4h.02"
              strokeWidth={2}
              strokeLinecap="round"
              strokeLinejoin="round"
            />
          </svg>
          <p className="mt-2 text-sm text-gray-600">No images uploaded</p>
          <button
            onClick={() => fileInputRef.current?.click()}
            className="mt-4 text-amber-600 hover:text-amber-700 text-sm font-medium"
          >
            Upload your first image
          </button>
        </div>
      ) : (
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
          {images.map((image) => (
            <div
              key={image.id}
              className="relative group rounded-lg overflow-hidden border-2 border-gray-200 hover:border-amber-500 transition-colors"
            >
              <div className="aspect-square relative">
                <img
                  src={image.url}
                  alt="Product"
                  className="w-full h-full object-cover"
                />
                {image.is_primary && (
                  <div className="absolute top-2 left-2 bg-amber-600 text-white px-2 py-1 rounded text-xs font-medium">
                    Primary
                  </div>
                )}
              </div>

              <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-50 transition-opacity flex items-center justify-center opacity-0 group-hover:opacity-100">
                <div className="flex gap-2">
                  {!image.is_primary && (
                    <button
                      onClick={() => handleSetPrimary(image.id)}
                      className="px-3 py-1 bg-white text-gray-900 rounded text-sm font-medium hover:bg-gray-100"
                      title="Set as primary"
                    >
                      Set Primary
                    </button>
                  )}
                  <button
                    onClick={() => handleDeleteImage(image.id)}
                    className="px-3 py-1 bg-red-600 text-white rounded text-sm font-medium hover:bg-red-700"
                    title="Delete"
                  >
                    Delete
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}

      <p className="text-sm text-gray-500">
        {images.length} image(s) uploaded. The primary image will be displayed in product listings.
      </p>
    </div>
  );
}
